<template>
  <div class="home">
    <!-- 分页_S -->
    <div class="pagination">
      <h3>分页——————————————————————————————————————————————————</h3>
      <div class="block">
        <p class="demonstration">Simple</p>
        <el-pagination layout="prev, pager, next" :total="50"></el-pagination>
      </div>
      <div class="block">
        <p class="demonstration">Basic</p>
        <el-pagination layout="prev, pager, next" :total="1000"></el-pagination>
      </div>
      <div class="block">
        <p class="demonstration">Has Background</p>
        <el-pagination background layout="prev, pager, next" :total="1000"></el-pagination>
      </div>
      <div class="block">
        <p class="demonstration">Combination</p>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage1"
          :page-size="100"
          layout="total, prev, pager, next"
          :total="1000"
        ></el-pagination>

        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage2"
          :page-sizes="[100, 200, 300, 400]"
          :page-size="100"
          layout="sizes, prev, pager, next"
          :total="1000"
        ></el-pagination>

        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage3"
          :page-size="100"
          layout="prev, pager, next, jumper"
          :total="1000"
        ></el-pagination>

        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage4"
          :page-sizes="[100, 200, 300, 400]"
          :page-size="100"
          layout="total, sizes, prev, pager, next, jumper"
          :total="400"
        ></el-pagination>
      </div>
    </div>
    <!-- 分页_E -->
    <!--按钮_S-->
    <div class="font">
      <h3>按钮——————————————————————————————————————————————————</h3>
      <p>此按钮支持界面中的一下主要操作 色值为标准色（可点击状态） 灰色为不可点击状态</p>

      <el-button type="primary">Button</el-button>
      <el-button type="info" disabled>Button</el-button>

      <p>此按钮用于问卷题目选择经过 时出现</p>

      <el-button type="primary" plain>Button</el-button>

      <p>此按钮用于问卷答案默认 时候的状态</p>

      <el-button>Button</el-button>
    </div>
    <!-- 按钮_E-->
    <!-- 复选框_S -->
    <div class="checkbox">
      <h3>复选框——————————————————————————————————————————————————</h3>
      <div>
        <el-checkbox label="设计行业（外包设计公司、独立设计师等）" border></el-checkbox>
      </div>
    </div>
    <!-- 复选框_E -->
    <!-- 单选框_S -->
    <div class="radio">
      <h3>单选框——————————————————————————————————————————————————</h3>
      <div>
        <el-radio v-model="radio7" label="1" border>设计行业（外包设计公司、独立设计师等）</el-radio>
        <el-radio v-model="radio7" label="2" border>设计行业（外包设计公司、独立设计师等）</el-radio>
      </div>
    </div>
    <!-- 单选框_E -->
    <!-- input_S -->
    <div class="input">
      <h3>input——————————————————————————————————————————————————</h3>
      <div style="width:40%;">
        <el-form
          :model="ruleForm2"
          status-icon
          :rules="rules2"
          ref="ruleForm2"
          label-width="100px"
          class="demo-ruleForm"
          :show-message="false"
        >
          <el-form-item label prop="pass">
            <el-input type="password" v-model="ruleForm2.pass" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!-- input_E -->
    <!-- tabs_S -->
    <div class="tabs">
      <h3>tabs——————————————————————————————————————————————————</h3>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="New York" name="first">New York</el-tab-pane>
        <el-tab-pane label="Chicago" name="second">Chicago</el-tab-pane>
        <el-tab-pane label="London" name="third">London</el-tab-pane>
        <el-tab-pane label="Shanghai" name="fourth">Shanghai</el-tab-pane>
      </el-tabs>
    </div>

    <!-- tabs_E -->
  </div>
</template>

<script>
export default {
  methods: {
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    handleClick(tab, event) {
      console.log(tab, event);
    }
  },
  data() {
    var checkAge = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("年龄不能为空"));
      }
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error("请输入数字值"));
        } else {
          if (value < 18) {
            callback(new Error("必须年满18岁"));
          } else {
            callback();
          }
        }
      }, 1000);
    };
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.ruleForm2.checkPass !== "") {
          this.$refs.ruleForm2.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.ruleForm2.pass) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      currentPage1: 5,
      currentPage2: 5,
      currentPage3: 5,
      currentPage4: 4,
      radio7: "1",
      activeName: "first",
      ruleForm2: {
        pass: "",
        checkPass: "",
        age: ""
      },
      rules2: {
        pass: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }],
        age: [{ validator: checkAge, trigger: "blur" }]
      }
    };
  }
};
</script>
<style lang="scss">
.home {
  /* 分页*/
  .pagination {
    .block {
      margin-top: 50px;
      .demonstration {
        margin-top: 0;
        margin-bottom: 30px;
      }
      .el-pagination {
        margin-bottom: 30px;
      }
    }
  }
  /* 按钮*/
}
</style>
<style>
/* 单选框、复选框样式 */
.home .radio .el-radio.is-bordered.is-checked,
.home .checkbox .el-checkbox.is-bordered.is-checked {
  background: #2673ff;
  border-color: #2673ff;
}
.home .radio .el-radio__input.is-checked .el-radio__inner,
.home .checkbox .el-checkbox__input.is-checked .el-checkbox__inner,
.home .checkbox .el-checkbox__input.is-indeterminate .el-checkbox__inner {
  border-color: #fff;
  background: #2673ff;
}
.home .radio .el-radio__input.is-checked + .el-radio__label,
.home .checkbox .el-checkbox__input.is-checked + .el-checkbox__label {
  color: #fff;
}
</style>
